<template>
	<section class="toy-app">
		<toy-filter :filterBy="filterBy" :toyTypes="toyTypes" @set-filter="setFilter" />
		<toy-list :toys="toys" @remove-toy="onRemoveToy" />
		<router-link v-if="isAdmin" to="/toy-edit">+</router-link>
	</section>
</template>
<script>
import toyList from '../cmps/toy-list.vue'
import toyFilter from '../cmps/toy-filter.vue'

export default {
	components: { toyList, toyFilter },
	computed: {
		toys() {
			return this.$store.getters.toys
		},
		filterBy() {
			return { ...this.$store.getters.filterBy }
		},
		toyTypes() {
			const types = this.$store.getters.toys.map(t => t.type)
			return [...new Set(types)]
		},
		isAdmin() {
			return this.$store.getters.loggedinUser.isAdmin
		},
		loggedinUser() {
			return this.$store.getters.loggedinUser
		},
	},
	methods: {
		onRemoveToy(toyId) {
			this.$store.dispatch({ type: 'removeToy', toyId })
		},
		setFilter(filterBy) {
			this.$store.commit({ type: 'setFilter', filterBy })
			this.$store.dispatch({ type: 'loadToys' })
		},
	},
	created() {
		if (!this.loggedinUser) {
			this.$router.push('/login')
		} else {
			this.$store.dispatch({ type: 'loadToys' })
		}
	},
}
</script>
