<template>
	<section v-if="toy" class="toy-details">
		<p>Name: {{ toy.name }}</p>
		<p>Price: ${{ toy.price }}</p>
		<p>Type: {{ toy.type }}</p>
		<p>Created: {{ toy.createdAt | moment('from') }}</p>
		<p :style="inStock.color">{{ inStock.txt }}</p>
		<ul>
			<h2>Reviews:</h2>
			<li v-for="review in reviews" :key="review.id">
				<p>{{ review.txt }}</p>
				<p>{{ review.at | moment('from') }}</p>
			</li>
		</ul>
		<el-button @click="toggleReview" type="success">Add a review</el-button>
		<section v-if="isReviewOpen">
			<input type="text" v-model="reviewTxt" placeholder="Review text" />
			<el-button @click="addReview">Add</el-button>
			<p v-if="error" class="error">{{ error.msg }}</p>
		</section>
	</section>
</template>

<script>
import { toyService } from '../services/toy-service.js'
import { reviewService } from '../services/review.service.js'

export default {
	data() {
		return {
			toy: null,
			reviewTxt: '',
			isReviewOpen: false,
			error: null,
		}
	},
	methods: {
		formattedDate(timestamp) {
			return new Date(timestamp).toLocaleString()
		},
		toggleReview() {
			this.isReviewOpen = !this.isReviewOpen
		},
		addReview() {
			const review = reviewService.createEmptyReview(this.reviewTxt, this.toy._id)
			this.$store.dispatch({ type: 'addReview', review })
			if (!review.txt) {
				this.error = {
					msg: 'Review field cannot be blank!',
				}
			}
			this.reviewTxt = ''
			this.error = null
		},
	},
	computed: {
		inStock() {
			return this.toy.inStock
				? { txt: 'In stock', color: { color: '#338800' } }
				: { txt: 'Not in stock', color: { color: '#ff0000' } }
		},
		loggedinUser() {
			return this.$store.getters.loggedinUser
		},
		reviews() {
			const reviews = this.$store.getters.reviews
			console.log(reviews)
			return reviews
		},
	},
	async created() {
		const { toyId } = this.$route.params
		if (toyId) {
			try {
				const toy = await toyService.getById(toyId)
				this.toy = toy
				this.$store.dispatch({
					type: 'loadReviews',
					filterBy: { aboutToyId: toyId },
				})
			} catch (err) {
				console.log(err)
			}
		}
	},
}
</script>
