<template>
	<section class="toy-item">
		<img v-if="toy.imgUrl" :src="toy.imgUrl" alt="toy" />
		<p>{{ toy.name }}</p>
		<p>${{ toy.price }}</p>
		<button v-if="isAdmin" @click="onRemoveToy(toy._id)"></button>
		<router-link v-if="isAdmin" :to="'/toy-edit/' + toy._id"></router-link>
		<router-link :to="'/toy-app/' + toy._id"></router-link>
	</section>
</template>

<script>
export default {
	props: { toy: Object },
	methods: {
		onRemoveToy(toyId) {
			this.$emit('remove-toy', toyId)
		},
	},
	computed: {
		isAdmin() {
			return this.$store.getters.loggedinUser.isAdmin
		},
	},
}
</script>
