<template>
	<section v-if="filterBy" class="toy-filter">
		<el-input
			type="text"
			name="filter"
			placeholder="filter"
			v-model="filterBy.name"
			@input="setFilter"
			class="input"
		/>
		<el-select name="inStock" v-model="filterBy.inStock" @change="setFilter">
			<el-option value="" label="All">Any</el-option>
			<el-option value="TRUE" label="In stock">In stock</el-option>
			<el-option value="FALSE" label="Not in stock">Not in stock</el-option>
		</el-select>
		<el-select name="type" v-model="filterBy.type" @change="setFilter">
			<el-option value="" label="All">All</el-option>
			<el-option v-for="(type, idx) in toyTypes" :key="idx" :label="type" :value="type">{{
				type
			}}</el-option>
		</el-select>
		<el-select name="sort" v-model="filterBy.sortBy" @change="setFilter">
			<el-option value="" label="All">Any</el-option>
			<el-option value="NAME" label="Name">Name</el-option>
			<el-option value="PRICE" label="Price">Price</el-option>
		</el-select>
	</section>
</template>

<script>
export default {
	props: { filterBy: Object, toyTypes: Array },
	methods: {
		setFilter() {
			this.$emit('set-filter', this.filterBy)
		},
	},
}
</script>
