<template>
	<section class="login-page">
		<el-form class="login-form">
			<el-input type="text" name="username" placeholder="Username" v-model="userCred.username" />
			<el-input
				type="password"
				name="password"
				placeholder="password"
				v-model="userCred.password"
			/>
			<el-input v-if="isSignup" type="text" name="fullname" placeholder="full name" />
			<el-button type="primary" @click="send($event)">Login</el-button>
		</el-form>
		<section v-if="error" class="error">
			<p>{{ error.msg }}</p>
		</section>
	</section>
</template>

<script>
export default {
	data() {
		return {
			userCred: {
				username: '',
				password: '',
			},
			isSignup: false,
			error: null,
		}
	},
	methods: {
		async send(ev) {
			ev.preventDefault()
			try {
				await this.$store.dispatch({ type: 'login', userCred: this.userCred })
			} catch (err) {
				this.error = {
					msg: 'Authentication failed, please double check your credentials',
				}
				return
			}
			this.$router.push('/toy-app')
		},
	},
}
</script>

