import { reviewService } from '@/services/review.service.js'

export const reviewStore = {
	state: {
		reviews: [],
	},
	getters: {
		reviews(state) {
			return state.reviews
		},
	},
	mutations: {
		setReviews(state, { reviews }) {
			state.reviews = reviews
		},
		addReview(state, { review }) {
			state.reviews.push(review)
		},
	},
	actions: {
		async loadReviews({ commit }, { filterBy }) {
			try {
				const reviews = await reviewService.query(filterBy)
				commit({ type: 'setReviews', reviews })
			} catch (err) {
				console.log('Error: Cannot set reviews:', reviews)
				throw err
			}
		},
		async addReview({ commit }, { review }) {
			try {
				if (!review.txt) return Promise.reject('review field cannot be empty')
				const reviewToAdd = await reviewService.save(review)
				commit({ type: 'addReview', review: reviewToAdd })
				console.log('reviewToAdd', reviewToAdd)
				return reviewToAdd
			} catch (err) {
				console.log('Error: Cannot add review:', review)
				throw err
			}
		},
	},
}
