<template>
	<section class="dashboard">
		<pie-chart v-if="toys.length" :data="chartData" :options="chartOptions" class="pie-chart" />
		<pie-chart
			v-if="toys.length"
			:data="stockChartData"
			:options="chartOptions"
			class="pie-chart"
		/>
	</section>
</template>

<script>
import pieChart from '../cmps/pie-chart.vue'
import { utilService } from '../services/util.service.js'

export default {
	components: { pieChart },
	data() {
		return {
			chartOptions: {
				borderWidth: '10px',
				hoverBackgroundColor: 'red',
				hoverBorderWidth: '10px',
			},
		}
	},
	computed: {
		toys() {
			return this.$store.getters.toys
		},
		chartData() {
			return {
				labels: this.$store.getters.toys.map(t => t.type),
				datasets: [
					{
						label: 'Toys',
						backgroundColor: this.$store.getters.toys.map(() => utilService.getRandomColor()),
						data: this.$store.getters.toys.map(t => t.price),
					},
				],
			}
		},
		stockChartData() {
			return {
				labels: [...new Set(this.$store.getters.toysInStock.map(t => t.type))],
				datasets: [
					{
						label: 'Toys In Stock',
						backgroundColor: this.$store.getters.toysInStock.map(() =>
							utilService.getRandomColor()
						),
						data: [
							...new Set(
								this.$store.getters.toysInStock.map(t => {
									const count = this.$store.getters.toysInStock.reduce(
										(acc, toy) => (toy.type === t.type ? acc + 1 : acc),
										0
									)
									return ((count / this.$store.getters.toysInStock.length) * 100).toFixed(2)
								})
							),
						],
					},
				],
			}
		},
	},
	created() {
		this.$store.dispatch({ type: 'loadToys' })
	},
}
</script>
