<template>
	<section class="toy-edit">
		<form v-if="toy">
			<validation-provider rules="alpha" v-slot="{ errors }">
				<el-input
					type="text"
					name="name"
					placeholder="name"
					v-model="toy.name"
					@input="sendErrors(errors)"
				/>
				<span>{{ errors[0] }}</span>
			</validation-provider>

			<validation-provider rules="between:1,999" v-slot="{ errors }">
				<el-input
					type="text"
					name="price"
					placeholder="price"
					v-model.number="toy.price"
					@input="sendErrors(errors)"
				/>
				<span>{{ errors[0] }}</span>
			</validation-provider>

			<validation-provider rules="alpha" v-slot="{ errors }">
				<el-input
					type="text"
					name="type"
					placeholder="type"
					v-model="toy.type"
					@input="sendErrors(errors)"
				/>
				<span>{{ errors[0] }}</span>
			</validation-provider>

			<img
				v-if="isLoading"
				src="https://gifimage.net/wp-content/uploads/2018/06/transparent-loader-gif-13.gif"
				alt=""
			/>
			<label v-else>
				Upload Image:
				<img
					class="upload"
					src="https://cdn3.iconfinder.com/data/icons/web-icons-2/64/Cloud_Upload-256.png"
					alt=""
				/>
				<input type="file" name="toy-image" @change="onUploadImg" />
			</label>

			<el-button type="primary" @click="onSubmit">Submit</el-button>
		</form>
		<section v-if="isErrorShown" class="error">
			<p>Failed to submit because fields are not filled correctly!</p>
		</section>
	</section>
</template>

<script>
import { toyService } from '../services/toy-service.js'
import { ValidationProvider } from 'vee-validate'
import { uploadImg } from '@/services/img-upload.service.js'

export default {
	components: {
		ValidationProvider,
	},
	data() {
		return {
			toy: null,
			errors: [],
			isErrorShown: false,
			isLoading: false,
		}
	},
	methods: {
		onSubmit(ev) {
			ev.preventDefault()
			if (this.errors.length) {
				this.isErrorShown = true
				setTimeout(() => {
					this.isErrorShown = false
				}, 2500)
				return
			}
			this.$store.dispatch({ type: 'saveToy', toy: this.toy }).then(() => {
				this.$router.push('/toy-app')
			})
		},
		sendErrors(errors) {
			this.errors = errors
		},
		async onUploadImg(ev) {
			this.isLoading = true
			const { url } = await uploadImg(ev)
			this.toy.imgUrl = url
			this.isLoading = false
		},
	},
	created() {
		const { toyId } = this.$route.params
		if (toyId) {
			toyService.getById(toyId).then(toy => {
				this.toy = toy
			})
		} else this.toy = toyService.createEmptyToy()
	},
}
</script>
