<template>
	<section>
		<ul v-if="toys" class="toy-list">
			<li v-for="toy in toys" :key="toy._id">
				<toy-preview :toy="toy" @remove-toy="onRemoveToy" />
			</li>
		</ul>
	</section>
</template>

<script>
import toyPreview from './toy-preview.vue'
export default {
	components: { toyPreview },
	props: { toys: Array },
	methods: {
		onRemoveToy(toyId) {
			this.$emit('remove-toy', toyId)
		},
	},
}
</script>
