import { toyService } from '../../services/toy-service.js'

export const toyStore = {
	state: {
		toys: [],
		filterBy: {
			name: '',
			inStock: '',
			type: '',
			sortBy: '',
		},
	},
	getters: {
		toys(state) {
			return state.toys
		},
		filterBy(state) {
			return state.filterBy
		},
		toysInStock(state) {
			return state.toys.filter(t => t.inStock)
		},
	},
	mutations: {
		setToys(state, { toys }) {
			state.toys = toys
		},
		removeToy(state, { toyId }) {
			const idx = state.toys.findIndex(t => t._id === toyId)
			state.toys.splice(idx, 1)
		},
		addToy(state, { toy }) {
			state.toys.push(toy)
		},
		updateToy(state, { toy }) {
			const idx = state.toys.findIndex(t => t._id === toy._id)
			state.toys.splice(idx, 1, toy)
		},
		setFilter(state, { filterBy }) {
			state.filterBy = { ...filterBy }
		},
	},
	actions: {
		loadToys(context) {
			return toyService
				.query(context.getters.filterBy)
				.then(toys => {
					context.commit({ type: 'setToys', toys })
					return toys
				})
				.catch(err => {
					console.log('Error: Cannot load toys', err)
					throw err
				})
		},
		removeToy({ commit }, payload) {
			return toyService
				.remove(payload.toyId)
				.then(() => {
					commit(payload)
				})
				.catch(err => {
					console.log('Error: Cannot remove toy', err)
					throw err
				})
		},
		saveToy({ commit }, payload) {
			const type = payload.toy._id ? 'updateToy' : 'addToy'
			return toyService
				.save(payload.toy)
				.then(savedToy => {
					commit({ type, toy: savedToy })
					return savedToy
				})
				.catch(err => {
					console.log('Error: Cannot save toy', payload.toy, err)
					throw err
				})
		},
	},
}
