import { httpService } from './http.service.js'

export const toyService = {
	query,
	getById,
	remove,
	save,
	addReview,
	createEmptyToy,
	createEmptyReview,
}

const TOY_URL = process.env.NODE_ENV !== 'development' ? '/api/toy/' : 'toy/'

async function query(filterBy) {
	return await httpService.get(TOY_URL, { params: filterBy })
}

async function getById(toyId) {
	return await httpService.get(`${TOY_URL}${toyId}`)
}

async function remove(toyId) {
	return await httpService.delete(`${TOY_URL}${toyId}`)
}

async function save(toy) {
	return toy._id
		? await httpService.put(`${TOY_URL}${toy._id}`, toy)
		: await httpService.post(TOY_URL, toy)
}

async function addReview(toy, review) {
	return await httpService.post(`${TOY_URL}${toy._id}/review`, review)
}

function createEmptyToy() {
	return {
		_id: '',
		price: 0,
		name: '',
		type: '',
		createdAt: Date.now(),
		inStock: true,
		reviews: [],
	}
}

function createEmptyReview(txt) {
	return {
		txt,
		at: Date.now(),
	}
}

