import { httpService } from './http.service.js'

export const reviewService = {
	query,
	getById,
	remove,
	save,
	createEmptyReview,
}

const REVIEW_URL = process.env.NODE_ENV !== 'development' ? '/api/review/' : 'review/'

async function query(filterBy) {
	return await httpService.get(REVIEW_URL, { params: filterBy })
}

async function getById(reviewId) {
	return await httpService.get(`${REVIEW_URL}${reviewId}`)
}

async function remove(reviewId) {
	return await httpService.delete(`${REVIEW_URL}${reviewId}`)
}

async function save(review) {
	return review._id
		? await httpService.put(`${REVIEW_URL}${review._id}`, review)
		: await httpService.post(REVIEW_URL, review)
}

function createEmptyReview(txt, aboutToyId) {
	return {
		txt,
		at: Date.now(),
		byUserId: '',
		aboutToyId,
	}
}
