<template>
	<div class="about">
		<GmapMap
			:center="{ lat: 32, lng: 35 }"
			:zoom="7"
			map-type-id="terrain"
			style="width: 750px; height: 450px"
			ref="mapRef"
			class="map"
		>
			<GmapMarker
				:key="index"
				v-for="(m, index) in markers"
				:position="m.position"
				:clickable="true"
				:draggable="true"
				@click="center = m.position"
			/>
		</GmapMap>
		<section class="btn-container">
			<el-button
				type="primary"
				v-for="(m, index) in markers"
				:key="index"
				@click="moveTo(m.position.lat, m.position.lng)"
			>
				{{ m.name }}
			</el-button>
		</section>
	</div>
</template>

<script>
export default {
	data() {
		return {
			markers: [
				{ position: { lat: 32.419407, lng: 34.918009 }, name: 'Hadera' },
				{ position: { lat: 32.109333, lng: 34.855499 }, name: 'Tel Aviv' },
				{ position: { lat: 32.3075945, lng: 34.8755032 }, name: 'Natenya' },
			],
		}
	},
	methods: {
		moveTo(lat, lng) {
			this.$refs.mapRef.$mapPromise.then(map => {
				map.panTo({ lat, lng })
			})
		},
	},
}
</script>
